<template>
    <div>
        <el-table
                :data="tableData"
                max-height="560"
                :row-class-name="tableRowClassName">
            <el-table-column type="expand">
                <template slot-scope="props">
                    <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item label="后厨状态">
                            <span>{{ props.row.cook_status}}</span>
                        </el-form-item>
                        <el-form-item label="备注">
                            <span>{{ props.row.comments}}</span>
                        </el-form-item>
                        <el-form-item label="菜单列表">
                            <el-table
                                    style="width: 95%"
                                    :data="props.row.menus">
                                <el-table-column
                                        prop="name"
                                        label="图片">
                                    <template scope="scope">
                                        <img class="menu-img" :src="'/Static/'+scope.row.img"/>
                                    </template>
                                </el-table-column>
                                <el-table-column
                                        prop="name"
                                        label="菜品">
                                </el-table-column>
                                <el-table-column
                                        prop="num"
                                        label="数量">
                                </el-table-column>
                                <el-table-column
                                        prop="price"
                                        label="价格">
                                    <template scope="scope">
                                        <span style="color: red"> ￥ {{scope.row.price*scope.row.num}}</span>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </el-form-item>
                    </el-form>
                </template>
            </el-table-column>
            <el-table-column
                    prop="id"
                    label="订单号">
            </el-table-column>
            <el-table-column
                    prop="user"
                    label="用户">
            </el-table-column>
            <el-table-column
                    prop="time"
                    label="时间"
                    sortable>
            </el-table-column>
            <el-table-column
                    prop="desk"
                    label="桌号"
                    width="60px">
            </el-table-column>
            <el-table-column
                    prop="total"
                    label="总金额"
                    width="100px">
                <template slot-scope="scope">
                    <p style="color: red">￥ {{ scope.row.total }}</p>
                </template>
            </el-table-column>
            <el-table-column
                    prop="order_status"
                    label="付款方式"
                    width="120px">
            </el-table-column>
        </el-table>
        <el-pagination
                background
                layout="prev, pager, next, jumper, ->, total, slot"
                @current-change="CurrentChange"
                :total="total"
                :page-size="10">
        </el-pagination>
    </div>
</template>

<script>
    export default {
        name: "order_list",
        data() {
            return {
                tableData: [],
                total: 0
            }
        },
        created: function () {
            this.axios.get('/backoffice/order_list/1').then((response) => {
                this.tableData = response.data.detail;
                this.total = response.data.total;
            })
        },
        methods: {
            CurrentChange(page) {
                this.axios.get('/backoffice/order_list/' + page).then((response) => {
                    this.tableData = response.data.detail;
                })
            },
            tableRowClassName({row}) {
                if (row.order_status == '未付款') {
                    return 'warning-row';
                } else {
                    return 'success-row';
                }
            },
            handleEdit(id) {
                this.$router.push('/order_edit/' + id);
            },
        }
    }
</script>


<style>
    .menu-img {
        width: 50px;
        height: 50px;
    }

    .el-table .warning-row {
        background: oldlace;
    }

    .el-table .success-row {
        background: #f0f9eb;
    }

    .demo-table-expand {
        font-size: 0;
    }

    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 100%;
    }
</style>
