<template>
    <div>
        <h1>今日数据</h1>
        <ve-pie :data="circle_data"></ve-pie>
        <h4>总计： ￥{{circle_data.total}}</h4>
    </div>
</template>

<script>
    export default {
        name: "revenue",
        data: function () {
            return {
                circle_data: {
                    columns: ['name', 'value'],
                    rows: null,
                    total: 0
                }
            }
        },
        created: function () {
            this.axios.get('/backoffice/revenue/today/0/0/0/').then((response) => {
                this.circle_data.rows = response.data.circle;
                this.circle_data.total = response.data.total;
            })
        },
    }
</script>

<style scoped>
    h1 {
        text-align: center;
    }

    h4 {
        color: brown;
        text-align: right;
    }
</style>