<template>
    <div>
        <el-table
                :data="tableData"
                style="width: 100%">
            <el-table-column
                    prop="fields.Name"
                    label="昵称">
            </el-table-column>
            <el-table-column
                    prop="fields.Session"
                    label="Session">
            </el-table-column>
            <el-table-column
                    prop="fields.MRP"
                    label="积分">
            </el-table-column>
        </el-table>
        <el-pagination
                background
                layout="prev, pager, next, jumper, ->, total, slot"
                @current-change="CurrentChange"
                :page-size="10">
        </el-pagination>
    </div>
</template>


<script>
    export default {
        name: "vip",
        data() {
            return {
                tableData: []
            }
        },
        created: function () {
            this.axios.get('/backoffice/Vip/1/').then((response) => {
                this.tableData = response.data;
            })
        }, methods: {
            CurrentChange(page) {
                this.axios.get('/backoffice/Vip/' + page).then((response) => {
                    this.tableData = response.data;
                })
            },
        }
    }
</script>

